// lib/api.ts
import axios, { AxiosError } from 'axios';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'https://elegant-charity-710d3644d3.strapiapp.com/api';

// ✅ Enhanced axios instance with proper config
const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 5000, // ✅ Reduced to 5 seconds
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
  },
});

// ✅ Request interceptor for debugging
api.interceptors.request.use(
  (config) => {
    console.log(`🚀 API Request: ${config.method?.toUpperCase()} ${config.url}`);
    return config;
  },
  (error) => {
    console.error('❌ Request Error:', error);
    return Promise.reject(error);
  }
);

// ✅ Response interceptor for better error handling
api.interceptors.response.use(
  (response) => {
    console.log(`✅ API Response: ${response.status} - ${response.config.url}`);
    return response;
  },
  (error: AxiosError) => {
    if (error.code === 'ECONNABORTED') {
      console.error('⏱️ Request timeout:', error.config?.url);
    } else if (error.response?.status === 404) {
      console.error('🔍 Resource not found:', error.config?.url);
    } else if (error.response?.status >= 500) {
      console.error('🔥 Server error:', error.response?.status);
    } else {
      console.error('❌ API Error:', error.message);
    }
    return Promise.reject(error);
  }
);

// ✅ Fixed Types with proper tags handling
